'use strict';

// MODULES //

import enum2str from '@stdlib/ndarray-base-output-policy-enum2str';
import str2enum from '@stdlib/ndarray-base-output-policy-str2enum';


// MAIN //

/**
* Returns the enumeration constant associated with a supported ndarray data type policy value.
*
* ## Notes
*
* -   Downstream consumers of this function should **not** rely on specific integer values (e.g., `SAME == 0`). Instead, the function should be used in an opaque manner.
*
* @param {*} policy - policy value
* @returns {(integer|null)} enumeration constant or null
*
* @example
* var v = resolve( 'same' );
* // returns <number>
*/
function resolve( policy ) {
	var t = ( typeof policy );
	if ( t === 'number' ) {
		return ( enum2str( policy ) ) ? policy : null;
	}
	if ( t === 'string' ) {
		return str2enum( policy );
	}
	return null;
}


// EXPORTS //

export default resolve;
